* {
  margin: 0;
  padding: 0;
}

body {
  background-color: antiquewhite !important;
}

.shortenForm {
  display: flex;
  flex-direction: column;
  width: 80%;
  max-width: 600px;
  margin: 0 auto;
  margin-top: 15%;
  & > div {
    margin: 2rem 0;
  }
  .shortenButton {
    width: 40%;
    align-self: center;
  }
  .shortUrlInput {
    margin-top: 5rem;
    margin-bottom: 0;
  }
  .copyAlert {
    max-width: 150px;
    color: rgb(54, 49, 49);
    background-color: antiquewhite;
    border-radius: 1rem;
    align-self: flex-end;
    opacity: 0.8;
    height: 45px;
    margin-top: 0;
  }
}

a {
  text-decoration: none;
}
